.container {
  position: absolute;
  height: 28vw;
  width: 12vw;
  left: 66vw;
  background-color:  rgba(22, 21, 21, 0.89);
  top: 0;
  bottom: 0;
  margin-left: 0;
  margin: auto;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  box-shadow: 0 38px 76px rgba(0,0,0,0.30), 0 30px 24px rgba(0,0,0,0.22);
  transition: width 0.7s;
  text-align: center;
}

.btn {
  top: 9vw;
  width: 8vw;
  height: 3vw;
  position: relative;
  margin: 1vw;
  background-color: #cacdd4;
  border-width: 0;
  border-radius: 10px;
  font-size: 1.5vw;
  font-family: CeliaGarcia, Calibri, sans-serif;
  font-style: italic;
  font-weight: 700;
  outline: none;
  border: 0.3vw solid  #cacdd4;
  transition: all 0.5s;
}

.btn:hover {
  cursor: pointer;
  background-color: transparent;
  color:  #cacdd4;
  box-shadow: 0px 0px 10px 0px rgb(0, 0, 0), 0px 0px 10px 0px rgb(0, 0, 0);
}